
import header from '../public/header'
import footer from '../public/footer'
import banner from '../../assets/banner_2.jpg'
import about from '../../assets/about.jpg'
import carousel_bg from '../../assets/carousel_bg.jpg'
import carousel_item from '../../assets/carousel_item.jpg'
import Icon_2 from '../../assets/Icon_2.png'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'


export default {
  components: {	
		headerV : header,
		footerV : footer,
		Swiper,
		SwiperItem
  },
  data () {
    return {
    	banner : [banner],
    	carousel_bg : "background:url('"+ carousel_bg +"') center center no-repeat",
    	about : [
    		{
    			src : about,
    			title : '',
    			content : 'HTML5',
    			btn : ''
    		}
    	],
      carousel : [
    		{
    			src : carousel_item,
    			desc : '',
    			title : '',
    		},
    		{
    			src : carousel_item,
    			desc : '',
    			title : '',
    		},
    		{
    			src : carousel_item,
    			desc : '',
    			title : '',
    		}
    	],
    	fn : [
    		{
    			src : Icon_2,
    			title : '',
    			content : 'HTML5'
    		},
    		{
    			src : Icon_2,
    			title : '',
    			content : 'HTML5'
    		},
    		{
    			src : Icon_2,
    			title : '',
    			content : 'HTML5'
    		},
    	],
    }
  }
}



